idth: 40px;\n      height: 30px;\n      line-height: 30px;\n      outline: none!important;\n      &.show{\n        right: 0px;\n        left: inherit;\n      }\n      &:focus, &:active{\n        outline: none;\n        box-shadow: none!important;\n      }\n    }\n    &.open{\n      right: 10px;\n    }\n\n  }\n  table {\n    color: #333;\n    background: #eee;\n    width: 100%;\n    //border-collapse: collapse;\n    border-spacing: 0;\n    border: 1px solid #ccc;\n  }\n\n  td, th {\n    border: 1px solid transparent;\n    height: 25px;\n\n  }\n\n  th {\n    background: #DFDFDF;\n    font-weight: normal;\n  }\n\n  td {\n    background: #FAFAFA;\n    text-align: center;\n    input{\n      border: 1px solid #ccc;\n      background: none;\n      height: inherit;\n      margin: 0;\n      padding: 0 3px;\n      width: 100%;\n      transition: all 0.3s;\n      //&:active, &:focus{\n      //  outline: none;\n      //  color: #fff;\n      //  background: #333;\n      //}\n    }\n    .dms{\n      input{\n        border: 1px solid #ccc;\n        width: 37px;\n      }\n    }\n  }\n  tr:nth-child(even) td { background: #F1F1F1; }\n  tr:nth-child(odd) td { background: #FEFEFE; }\n\n  .point-coordinate{\n    width: 600px;\n    position: absolute;\n    top: 1px;\n    left: 1px;\n    z-index: 1000;\n  }\n}\n.geographic-map, #geographic{\n  //margin-top: 52px;\n  width: 100%;\n  height: 450px;\n\n  position: relative;\n  &.open-side{\n    width: calc(100% - 320px);\n  }\n\n  #base-maps{\n    z-index: 700;\n    position: absolute;\n    bottom: 0;\n    left: 0;\n    ul{\n      li{\n        list-style: none;\n        display: inline-block;\n\n        a{\n          background: #fff;\n          color: #0079c1;\n          padding: 4px 8px;\n          height: 22px;\n          line-height: 22px;\n          margin-right: 2px;\n          &.active{\n            background: #0079c1;\n            color: #fff;\n          }\n        }\n      }\n    }\n  }\n  .leaflet-control-attribution{\n    displa